<template>
	<div class="RFPick_confirm">
		<header><i class="iconfont icon-xiangzuo1" @click="ToRFPick"></i><span>拣货</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<span>库位</span>
				<input  readonly  v-model="input_LibraryCode"></input>
			</div>
			<div>
				<span>托盘编号</span>
				<input   readonly  v-model="input_TrayNum"></input>
			</div>
			<div>
				<span>商品代码</span>
				<input   readonly  v-model="input_CommodityCode"></input>
			</div>

			<div class="goodsName">
				<span>商品名称</span>
				<textarea v-model="input_CommodityName"   readonly></textarea>
			</div>
			<div class="goodsName property">
				<span>批次属性</span>
				<textarea  readonly>
货物状态:{{lottable02}}
生产批号:{{lottable03}}
生产日期:{{lottable04}}
到期日期:{{lottable05}}
				</textarea>
			</div>
			<div>
				<span>待拣数量</span>
				<input   readonly  v-model="input_ToBeQuantity"></input>
			</div>
			<div>
				<span>拣货数量</span>
				<input  type="number"   @focus="g_focus('input_AmountQuantity')"    @keyup.enter="submit"  ref="input_AmountQuantity"   class="write_input" placeholder="必填"  v-model="input_AmountQuantity"></input>
				<i v-if="input_AmountQuantity" @click="clearInput('input_AmountQuantity')" class="iconfont icon-cuowu"></i>
			</div>
			<div class="g_switchTab">{{nowData}}/{{totalData}}</div>
			<p class="warningInfo">{{warningInfo}}</p>
			<div class="g_doubleBtn">
				<div @click="submit">确认</div>
				<div @click="skip">跳过</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"RFPick_confirm",
		data(){
			return{
				input_LibraryCode:'',
				input_TrayNum:'',
				input_CommodityCode:'',
				input_CommodityName:'',
				lottable02:'',//货物状态
				lottable03:'',
				lottable04:'',
				lottable05:'',
				input_ToBeQuantity:'',//待拣数量
				input_AmountQuantity:'',//拣货数量
				RFPick_SubmitData:"",//
				stripIndex:0,//当前条目
				warningInfo:"",//警告信息
				nowData:1,//当前查看的是第几条数据
				totalData:0,//一共几条数据
				StateOfGoodsData:[],//货物状态数组
			}
		},
		mounted() {

			this.$refs['input_AmountQuantity'].focus();
			window.QRScanAjax = this.QRScanAjax;
			// 控制字符长度
			$("input").attr("maxlength","50");
		},
		created(){

			var that = this;

			console.log(that.$store.state.RFPick_SubmitData)

			if(that.$store.state.RFPick_SubmitData.length){

				this.changePageData(0);

				this.totalData = that.$store.state.RFPick_SubmitData.length;
			}
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				this.submit();
				
			},
			// 返回上一页
			ToRFPick:function(){

				var TaskGroup = this.$store.state.RFPick_SubmitData[0].TaskGroup
				this.$router.push({
					name:"RFPick",
					query:{
						TaskGroup:TaskGroup
					}
				})
			},
			// 确认 按钮
			submit:function(){

				var that = this;

				if(!that.input_AmountQuantity){

					that.warningInfo = "请填写拣货数量";

					return;
				}else if(that.input_AmountQuantity > that.input_ToBeQuantity){

					that.warningInfo = "拣货数量不可大于待拣数量";

					return;
				}else if(that.input_AmountQuantity < 0 ){

					that.warningInfo = "拣货数量不可小于零";

					return;
				}else{
					that.warningInfo = "";
				}

				var RFReceiveUrl = this.HOST + "RFPickConfirm.do";

				var r_postData = {
					TaskDetailKey : that.$store.state.RFPick_SubmitData[that.stripIndex].TaskDetailKey,
					QtyPicked : that.input_AmountQuantity,
				}

				this.httpRequest(RFReceiveUrl,r_postData,function(res){

					if(parseInt(that.$store.state.RFPick_SubmitData.length)>parseInt(that.stripIndex+1)){

						that.stripIndex++;

						that.changePageData(that.stripIndex);

						that.nowData = that.stripIndex +1;

					}else{

						that.$router.push({
							name:"RFPick",
							query:{
								
							}
						})
					}
				},"input_AmountQuantity")
			},
			// 跳过 按钮
			skip:function(){

				var that = this;

				var RFReceiveUrl = this.HOST + "RFPickSkip.do";

				var r_postData = {
					TaskDetailKey : that.$store.state.RFPick_SubmitData[that.stripIndex].TaskDetailKey,
					QtyPicked : that.input_AmountQuantity,
				}

				this.httpRequest(RFReceiveUrl,r_postData,function(res){

					if(parseInt(that.$store.state.RFPick_SubmitData.length)>parseInt(that.stripIndex+1)){

						that.stripIndex++;

						that.changePageData(that.stripIndex);

						that.nowData = that.stripIndex +1;

					}else{

						that.$router.push({
							name:"RFPick",
							query:{
								
							}
						})
					}
				},"input_AmountQuantity")
			},
			// 改变页面数据
			changePageData:function(idx){

				var that = this;

				var RFPick_SubmitData = this.$store.state.RFPick_SubmitData;

				this.input_LibraryCode = RFPick_SubmitData[idx].FromLoc;//库位
				this.input_TrayNum = RFPick_SubmitData[idx].FromID;//托盘编号
				this.input_CommodityCode = RFPick_SubmitData[idx].Sku;//商品代码
				this.input_CommodityName = RFPick_SubmitData[idx].Descr;//商品名称
				this.lottable02 = RFPick_SubmitData[idx].Lottable02;//货物状态
				this.lottable03 = RFPick_SubmitData[idx].Lottable03;//生产批号
				this.lottable04 = RFPick_SubmitData[idx].Lottable04;//生产日期
				this.lottable05 = RFPick_SubmitData[idx].Lottable05;//到期日期
				this.input_ToBeQuantity = RFPick_SubmitData[idx].Qty;//待拣数量
				this.input_AmountQuantity = RFPick_SubmitData[idx].Qty;//拣货数量

				if(that.$store.state.StateOfGoodsData.length){

					that.StateOfGoodsData = that.$store.state.StateOfGoodsData;

					// 货物状态代码
					for(var j = 0;j < that.StateOfGoodsData.length ; j++){

						if(RFPick_SubmitData[idx].Lottable02 == that.StateOfGoodsData[j].Code){

							that.lottable02 = that.StateOfGoodsData[j].Descr;
						}
					}

				}else{

					var stateUrl = this.HOST + "GoodsStatus.do";

					that.httpRequest(stateUrl,{Code:"",Descr:""},function(res){

						console.log(res)

						that.StateOfGoodsData = res.data.data;

						that.$store.state.StateOfGoodsData = res.data.data;

						// 货物状态代码
						for(var j = 0;j < that.StateOfGoodsData.length ; j++){

							if(RFPick_SubmitData[idx].Lottable02 == that.StateOfGoodsData[j].Code){

								that.lottable02 = that.StateOfGoodsData[j].Descr;
							}
						}
					})
				}
			}
		}
	}
</script>
<style lang="less" scoped>
	.RFPick_confirm{
		.container{
			padding-top: 1/50rem;
			input,textarea{
				background-color:#f0f0f0;
				font-size: 29/50rem;
				border:none;
				outline: none;
			}
			&>div{
				width:100%;
				position: relative;
				box-sizing: border-box;
				margin-top: 20/50rem;
				overflow: hidden;
				span{
					width: 160/50rem;
					float: left;
					height: 55/50rem;
					line-height: 55/50rem;
					margin:0  20/50rem;
					text-align: right;
				}
				input{
					width:450/50rem;
					float: left;
					height:55/50rem;
					padding:0 10/50rem;
					
				}
				i{
					position:absolute;
					right:90/50rem;
					font-size: 30/50rem;
					top: 14/50rem;
				}
			}
			&>.goodsName{	
				height:100/50rem;
				margin-bottom: 15/50rem;
				overflow: hidden;
				&>span{
					width: 160/50rem;
					margin-right: 10/50rem;
					float: left;
					text-align: right;
				}
				&>textarea{
					width:450/50rem;
					height:100/50rem;
					padding:0 10/50rem;
					float:left;
					outline:none;
					margin-left: 10/50rem;
				}
			}
			p.warningInfo{
				padding-left: 180/50rem;
				box-sizing: border-box;
			}
			&>.property{
				height: 220/50rem;
				&>textarea{
					height:220/50rem;
				}
			}
			.write_input{
				background-color: #fff;
				padding: 0 10/50rem;
				border:1/50rem solid #bbb;
			}
			input.write_input:focus{ outline:#7BD039 solid 3/50rem; }
		}
	}
@media screen   and (max-width: 330px) and (min-width: 244px){
	.RFPick_confirm{
		.container{
			input,textarea{
				font-size: 35/50rem;
			}
			&>div{
				margin-top: 3/50rem;
				input{
					width:480/50rem;
				}
				i{
					right:75/50rem;
					font-size: 40/50rem;
					top: 10/50rem;
				}
			}
			&>.goodsName,&>.property{
				height: 70/50rem;
				margin-bottom: 3/50rem;
				&>textarea{
					height: 70/50rem;
					width: 480/50rem;
				}
			}
			&>.property{	
				height:160/50rem;
				&>textarea{
					height:160/50rem;
				}
			}
		}

		.g_switchTab{
			margin:0 auto;
			font-size: 38/50rem;
		}
	}
}
@media screen and (max-width: 243px){
	.RFPick_confirm{
		.container{
			input,textarea{
				font-size: 45/50rem;
			}
			&>div{
				margin-top: 10/50rem;
				span{
					width: 195/50rem;
					height: 70/50rem;
					line-height: 70/50rem;
					margin:0  20/50rem;
				}
				input{
					height: 70/50rem;
				}
				i{
					right:70/50rem;
					font-size: 48/50rem;
					top: 16/50rem;
				}
			}
			&>.goodsName,&>.property{
				height: 90/50rem;
				span{
					width: 195/50rem;
					height: 70/50rem;
					line-height: 70/50rem;
				}
				&>textarea{
					height: 90/50rem;
				}
			}
			&>.property{	
				height:200/50rem;
				&>textarea{
					height:200/50rem;
				}
			}
		}
	}
}
	@media screen and (max-width: 374px) {	
		.RFPick_confirm{
			.container{
				.write_input{
					border:3/50rem solid #bbb;
				}
			}
		}
	}
</style>


